import React from "react";
import photo from "../../img/Image_edited.jpg";

function Context() {
  return (
    <div className="row contentArea d-flex align-items-center my-5">
      <div className="col-12 col-sm-6 photoArea">
        <img
          src={photo}
          alt=""
          srcset=""
          className="photo w-100 p-5 rounded-pill"
        />
      </div>
      <div className="col-12 col-sm-6 text">
        <h4 className="mt-5 mb-3 name text-center">Merhaba Ben Dr. Murat Gökduman</h4>
        <p className="ben text-center">
          İş yeri hekimi, tam yığın web geliştiricisi ve oyun geliştiricisi olarak çeşitli alanlarda kendimi geliştiriyorum.
        </p>
        <p className="ben text-center">
          FullStack web geliştirme becerilerimle web uygulamaları geliştirmek için çalışıyorum. Ayrıca, Unity ile oyun geliştirmek benim için bir tutku haline geldi. Oyun dünyasında yaratıcı fikirlerimi hayata geçirirken, amatör fotoğrafçı kimliğimle de sanatın gücünden ilham alıyorum.
        </p>
        <p className="ben text-center">
          En büyük tutkum, sürekli olarak yeni şeyler öğrenmek ve kendimi geliştirmektir. Kitaplar, videolar ve uygulamalı deneyimler aracılığıyla sürekli öğrenme sürecimde ilerlerken, bu bilgi birikimini işlerime ve projelerime yansıtmak benim için heyecan verici bir deneyimdir.
        </p>
      </div>
    </div>
  );
}

export default Context;
